 <template>
	<view class="container">
    <view class="my_rescue">
      <view v-for="(item,index) in rescueItems" :key="item.id" class="my_rescue_bottom" @click="rescueDetail(item,index)">
        <view class="my_rescue_list">
          <view class="my_rescue_list_left">
            <image mode="scaleToFill" src="../../static/user/emergencyCourseList3x.png" />
          </view>
          <view class="my_rescue_list_center">
            <view class="my_rescue_list_center_brand">{{ item.startAddress }}</view>
            <view class="my_rescue_list_center_model">{{ item.createTime }}</view>
          </view>
          <view class="my_rescue_list_right"><image mode="scaleToFill" src="../../static/icon/right3x.png" /></view>
        </view>
      </view>
    </view>
		<view class="uni-loadmore" v-if="showLoadMore">{{loadMoreText}}</view>
		<view class="container">
			<uni-load-more :status="status" />
	</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
        rescueItems: [],
        rescueItemsArray: [],
        userName:null,
        pageNumber:1 ,//第几页
        pageSize:10,//一页有多少数据,
        loadMoreText: "加载中...",
        showLoadMore: false,
        pages: 0,
        status: 'more',
				statusTypes: [{
					value: 'more',
					text: '加载前',
					checked: true
				}, {
					value: 'loading',
					text: '加载中',
					checked: false
				}, {
					value: 'noMore',
					text: '没有更多',
					checked: false
				}],
			}
		},
    onLoad(){
      let userId = uni.getStorageSync('ID')
      this.$http.planRouteList({'createUserId':userId}).then(res=>{
        console.log('res =>',res)
        this.rescueItems = res.data.records
        this.pages = res.data.pages
        this.rescueItemsArray = res.data.records
      })
    },
    onUnload() {
			this.max = 0,
			this.data = [],
      this.status = 'more'
			this.showLoadMore = false
		},
		onReachBottom() {
      console.log(this._data)
      this.pageSize = 10
      console.log('this.pageNumber =>',this.pageNumber)
      console.log('this.pages =>',this.pages)
      console.log('this.pageNumber < this.pages =>',this.pageNumber < this.pages)
      if(this.pageNumber <= this.pages){
        this.pageNumber = this._data.pageNumber + 1
        // 上拉获取更多数据
        this.status = 'loading'
        this.gainMoreLoadingListData() 
      }
		},
    onChange(e) {
			this.status = e.detail.value
		},
		clickLoadMore(e) {
			uni.showToast({
				icon: 'none',
				title: "当前状态：" + e.detail.status
			})
		},
		onPullDownRefresh() {
			console.log('onPullDownRefresh');
			this.initData();
		},
		methods: {
      gainMoreLoadingListData: function () {
        let newdata = []
        let data = {
          pageSize: this.pageSize,
          pageNumber: this.pageNumber,
          createUserId: uni.getStorageSync('ID')
        }
        this.$http.planRouteList(data).then(res=>{
          let arr = res.data.records
          let array = this.rescueItemsArray
          let arrayItems = []
          array.forEach(e => {
            arrayItems.push(e)
          })
          arr.forEach(a => {
            arrayItems.push(a)
            array.push(a)
          })
          newdata = this.arrayItems
          console.log('arr =>',arr)
          console.log('array =>',array)
          console.log('arrayItems =>',arrayItems)
          if(arrayItems.length == res.data.total){
            this.status = 'noMore'
          }
        })
        // this.rescueItems = this.rescueItemsArray
      },
      rescueDetail(e){
        uni.navigateTo({
          url:'/pages/user/emergencyCourse?id='+e.id
        })
      },
		}
	}
</script>

